@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;800&display=swap);
body {
	margin: 0;
	font-family: "Montserrat", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	color: #302c48;
	font-weight: 600;
	height: 100vh;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar{position:fixed;width:300px;height:calc(100vh - 80px);display:flex;flex-direction:column;justify-content:space-around;align-items:center;border-right:2px #e5e9ec solid;padding-bottom:40px}.navbar .logo-container{display:flex;align-items:center;grid-gap:10px;gap:10px;margin-left:-20px}.navbar .logo-container .logo-icon{width:50px;height:50px;border-radius:10px;background-color:gray}.navbar .logo-container .logo-text{font-weight:800}.navbar .navbar-buttons-container{display:flex;flex-direction:column;grid-gap:10px;gap:10px;margin-top:20px}.navbar .navbar-buttons-container .button-container{display:flex;grid-gap:20px;gap:20px;padding:10px 30px;border-radius:10px;color:#b6bdc4;transition:all 0.3s}.navbar .navbar-buttons-container .button-container:hover{cursor:pointer;color:#38344f}.navbar .navbar-buttons-container .button-container.active{color:inherit}.navbar .bottom-info{display:flex;flex-direction:column;align-items:center}.navbar .bottom-info .bottom-img{width:250px}.navbar .bottom-info .img-text{text-align:center;color:#84858f}.navbar .bottom-info .button-settings{font-family:"Montserrat", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;font-weight:600;color:white;background-color:#38344f;padding:20px 40px;outline:none;border:none;border-radius:10px;margin-top:25px;transition:all 0.3s}.navbar .bottom-info .button-settings:hover{cursor:pointer;background-color:#312d44}

.layout{display:flex;margin-top:40px}.layout .layout-content{margin-left:300px;width:100%}

.header{display:flex;align-items:center;justify-content:space-between;padding:0 60px 0 0;margin-left:50px}.header .search-bar{display:flex;align-items:center;justify-content:flex-start;width:750px;padding:15px 20px;border-radius:20px;border:1.5px solid #e9e9e9}.header .search-bar .search-icon{display:flex;font-size:24px}.header .search-bar #search-input{border:none;outline:none;padding:0 20px;font-family:"Montserrat", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;font-weight:600;font-size:14px;width:100%;height:100%}.header .search-bar:focus-within{box-shadow:0 0 0 0.1rem #d8d8d8}.header .menu-icons-container{display:flex;grid-gap:30px;gap:30px;font-size:26px}.header .menu-icons-container *{cursor:pointer}

.heart-rate-main-card{border-radius:30px;display:flex;align-items:center;box-shadow:0px 0px 30px -20px rgba(0,0,0,0.75);background:linear-gradient(135deg, rgba(255,251,215,0.3) 70%, rgba(255,188,149,0.4) 100%);margin-top:30px}.heart-rate-main-card .title-graph-container{padding:30px 30px;border-radius:25px;width:75%;display:flex;flex:1 1;background-color:white}.heart-rate-main-card .title-graph-container .title-container{display:flex;flex-direction:column;grid-gap:20px;gap:20px;justify-content:center;flex:1 1;margin-left:20px}.heart-rate-main-card .title-graph-container .title-container .heart-icon{font-size:26px;display:flex}.heart-rate-main-card .title-graph-container .title-container .heart-title{font-size:22px;font-weight:700}.heart-rate-main-card .bpm-today-container{display:flex;flex-direction:column;align-items:center;grid-gap:8px;gap:8px;height:100%;overflow:hidden}.heart-rate-main-card .bpm-today-container .bpm-today{font-size:38px}.heart-rate-main-card .bpm-today-container .bpm-today-text{font-size:14px;text-align:center;color:#80868b;width:80%}

.heart-rate-second-card{display:flex;align-items:center;box-shadow:0px 0px 60px -30px rgba(0,0,0,0.35);padding:30px 50px 30px 30px;border-radius:20px}.heart-rate-second-card .icon{font-size:24px;display:flex;box-shadow:0px 0px 25px -1px rgba(0,0,0,0.15);padding:20px;border-radius:10px}.heart-rate-second-card .info-container{display:flex;flex-direction:column;margin-left:30px}.heart-rate-second-card .info-container .value{font-size:28px}.heart-rate-second-card .info-container .label{font-size:14px;color:#abb0b5}

.heart-rate .second-cards{display:flex;justify-content:space-around;margin-top:30px}

.todays-card{width:200px;display:flex;flex-direction:column;align-items:center;padding:20px 5px;border-radius:20px}.todays-card .card-img{width:170px;margin-top:-80px}.todays-card .card-text{margin-top:20px;text-align:center;width:80%;color:#80868b}.todays-card .value{padding:10px 40px;border-radius:10px;text-align:center;margin:20px 10px 0 0;background-color:white;font-weight:800}

.todays-activity{display:flex;justify-content:space-between;margin-top:80px}

.title.l{font-size:34px;font-weight:800;margin-left:50px}.title.s{font-size:18px;font-weight:700}

.section-header{display:flex;align-items:center;justify-content:space-between}.section-header .three-dots-button{display:flex;box-shadow:0px 0px 30px -14px rgba(0,0,0,0.75);padding:10px 15px;border-radius:10px;transition:all 0.3s}.section-header .three-dots-button:hover{cursor:pointer;box-shadow:0px 0px 30px -10px rgba(0,0,0,0.75)}

.section{width:750px;margin-top:20px;margin-left:50px}

.activity{flex:1 1}.activity .content{margin-top:45px;overflow-y:auto;height:100vh}

